* {
  padding: 0;
  margin: 0;
  border: 0; }

*,
*:before,
*:after {
  box-sizing: border-box; }

:focus,
:active {
  outline: none; }

a:focus,
a:active {
  outline: none; }

nav,
footer,
header,
aside {
  display: block; }

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 16px;
  color: #333; }

input,
button,
textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a,
a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit; }

.header {
  background: #4758ae; }
  .header__body {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 71px 0;
    position: relative; }
  .header__title {
    font-weight: bold;
    font-size: 48px;
    line-height: 59px;
    color: white; }
  .header__registration, .header__lang {
    position: absolute;
    top: 10px;
    right: 0; }
  .header__registration {
    right: 90px; }
    .header__registration a {
      display: inline-block;
      background: #f4f925;
      border: 1px solid #f4f925;
      border-radius: 10px;
      padding: 6px 19px;
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      color: #4758ae;
      text-transform: uppercase;
      transition: all ease-in .2s; }
      .header__registration a:hover {
        background: transparent;
        color: #fff; }
  .header__lang {
    top: 15px;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #ffffff;
    cursor: pointer; }

.imgf {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative; }
  .imgf > img {
    width: 0;
    height: 0;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden; }

.container {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px; }

body {
  background: url(../img/bg.png);
  font-family: "Montserrat", sans-serif; }

.main__body {
  padding: 25px 0;
  display: flex;
  flex-wrap: wrap; }

.main__items {
  display: flex; }

.main .item {
  min-width: 250px;
  margin: 15px 15px; }
  .main .item__body {
    padding: 185px 20px;
    position: relative;
    z-index: 1; }
    .main .item__body::before {
      position: absolute;
      content: "";
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.15); }
  .main .item__btn {
    background: rgba(244, 249, 37, 0.75);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 10px 38px;
    position: relative;
    z-index: 5;
    cursor: pointer;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    transition: all ease-in-out .1s; }
    .main .item__btn:hover {
      background: #f4f925; }
    .main .item__btn_fix {
      font-size: 16px;
      padding: 10px 0; }

.main__right {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .main__right .theme {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .main__right .theme__img {
      margin-bottom: 10px; }
    .main__right .theme__title {
      max-width: 228px;
      font-size: 24px;
      line-height: 29px;
      text-align: center;
      color: #000000;
      margin-bottom: 18px; }
  .main__right .statistics__title {
    text-align: center;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 17px; }
  .main__right .statistics__info {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #000;
    margin-bottom: 7px; }
    .main__right .statistics__info span {
      display: inline-block; }

.partners__body {
  padding: 26px 0;
  text-align: center; }

.partners__title {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin-bottom: 55px; }

.partners__items {
  display: flex;
  justify-content: space-between; }

.partners__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 201px; }

.partners__img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: 150px;
  background: #000000;
  border-radius: 50%;
  margin-bottom: 22px; }
  .partners__img_white {
    background: #fff; }

.partners__info {
  font-size: 16px;
  line-height: 20px; }

