/* Portrait tablets and medium desktops */
/* Portrait tablets and small desktops */
@media (min-width: 768px) and (max-width: 991px) {
  .main__body {
    padding: 25px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .main__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; } }

/* Landscape phones and portrait tablets */
@media (max-width: 767px) {
  .main__body {
    padding: 25px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .main__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; }
  .partners__items {
    justify-content: center;
    flex-wrap: wrap; }
  .partners__item {
    margin-bottom: 15px; }
  .header__title {
    text-align: center;
    font-weight: 700;
    font-size: 42px;
    line-height: 59px;
    color: #fff; } }

