.header {
	background: #4758ae;
	&__body {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 71px 0;
		position: relative;
	}
	&__title {
		font-weight: bold;
		font-size: 48px;
		line-height: 59px;
		color: white;
	}
	&__registration,
	&__lang {
		position: absolute;
		top: 10px;
		right: 0;
	}
	&__registration {
		right: 90px;
		a {
			display: inline-block;
			background: #f4f925;
			border: 1px solid #f4f925;
			border-radius: 10px;
			padding: 6px 19px;
			font-weight: 500;
			font-size: 18px;
			line-height: 22px;
			color: #4758ae;
			text-transform: uppercase;
			transition: all ease-in .2s;
			&:hover{
				background: transparent;
				color: #fff;
			}
		}
	}
	&__lang {
		top: 15px;
		font-weight: 500;
		font-size: 18px;
		line-height: 22px;
		color: #ffffff;
		cursor: pointer;
	}
}
